.board-list-item {
    margin: 15px;
    border-radius: 10px;
    padding: 24px;
    /* width: calc(384px - 24px - 24px);
    height: calc(508px - 24px - 24px); */
    width: 270px;
    height: 300px;

    display:flex;
    align-items: center;
    flex-direction: column;

    background-color: rgba(0,0,0,0.0);
    background-position: 50% 50%;
    background-size: 100% 100%;

    cursor:pointer;
}

.board-list-item:hover {
    background-color: rgba(0,0,0,0.1);
}
.board-list-item-box {
    display:flex;
    flex-direction: column;
    gap:10px;
}

.board-list-item-top {
   display:flex;
   flex-direction: column;
   gap:2px;
}
.board-list-item-nickname {
    color: rgba(0,0,0,0.7);
    font-size:13px;
    line-height: 140%;
    font-weight: 500;
}
.board-list-item-write-datetime{
    color: rgba(0,0,0,0.5);
    font-size:12px;
    font-weight: 400;;
    line-height: 140%;
}

.board-list-item-middle {
    display:flex;
    flex-direction: column;
    gap:10px;
    width: 250px;
    height:200px;
}
.board-list-item-title {

    flex: 2.5;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    height: 20px;

    font-size: 18px;
    font-weight: 500;
    line-height: 140%;

    color:rgba(0,0,0,1);

}
.board-list-item-content {

    color:rgba(0,0,0,0.7);
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    
    flex:7;
    overflow: hidden;
    text-overflow: ellipsis;
    
}
.board-list-item-title-image {
    
    width: 100%;
    height: 100px;
    object-fit: cover;

    background-position: 50% 50%;
    background-size: 100% 100%;
}
