#board-write-wrapper {

    display: flex;
    justify-content: center;

    background-color: rgba(0,0,0,0.05);
    
}
.board-write-container {
    padding: 100px 24px;
    background-color: rgba(255,255,255,255);
    width: 996px;
    min-height: 1300px;
}
.board-write-nickname-box {
    display:flex;
    
}
.board-write-nickname {
    font-size: 30px;
}
.board-write-box {
    display: flex;
    flex-direction: column;
    gap: 40px;
}
.board-write-title-box {
    width: 100%;
}
.board-write-title-textarea {
    width: 100%;
    
    border: none;
    outline: none;
    background: none;
    color: rgba(0,0,0,1);
    font-size: 30px;
    font-weight: 500;
    line-height: 140%;
    resize: none;

}
.board-write-content-box {
    width: 100%;
    display: flex;
    gap: 16px;
}
.board-write-content-textarea {
    flex: 1;

    border: none;
    outline: none;
    background: none;
    resize: none;

    color: rgba(0,0,0,0.7);
    font-size: 18px;
    font-weight: 500;
    line-height: 150%;
}
.board-write-images-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.board-write-image-box{
    width : 100%;
    position: relative;

}
.board-write-image {
    width: 100%;
}
.image-close {
    position: absolute;
    top: 20px;
    right: 20px;
}